.office {
  max-width: 333px;

  @media (max-width: $tablet-max-width) {
    flex-basis: 48%;
    max-width: unset;
  }

  @media (max-width: $mobile-max-width) {
    position: relative;

    &::after {
      position: absolute;
      content: "";
      width: 100vw;
      height: 1px;
      background-color: rgba(98, 105, 98, 0.2);
      bottom: 0;
      left: -15px;
    }
  }
}

.office h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $basic-black;
  margin: 0 0 25px 0;

  @media (max-width: $tablet-max-width) {
    padding-left: 1px;
  }

  @media (max-width: $mobile-max-width) {
    padding: 28px 0 28px;
    margin: 0;
    cursor: pointer;
    position: relative;
  }
}

.office p {
  font-size: 15px;
  line-height: 25px;
  color: rgba($basic-black, 0.5);
  margin: 0;

  @media (max-width: $mobile-max-width) {
    padding-right: 3px;
  }

  @media (max-width: $mobile-max-width) {
    padding-top: 4px;
    width: 100%;
  }
}
