@media (max-width: $mobile-max-width) {
  .phones {
    padding-bottom: 27px;
  }
}

.phones__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.phones__item a {
  text-decoration: none;
  font-size: 15px;
  line-height: 25px;
  color: rgba($basic-black, 0.5);

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}
